<template>
  <div id="radio">
    <Information :title="title" :artist="artist" :picture="picture" />
    <Player
      @next-track="musicNext()"
      @prev-track="musicPrev()"
      @play-track="musicPlay()"
      @pause-track="musicPause()"
    />
  </div>
</template>

<script>
  import Information from '../components/Information';
  import Player from '../components/Player.vue';
  import {
    myAudio,
    myAudio2,
    musicUrl,
    setThisSong,
    getThisSong,
    setSongIndex,
    getSongIndex,
    setSongName
  } from '../assets/js/Audio';
  import $ from 'jquery';
  import Axios from 'axios';
  export default {
    name: 'radio',
    props: ['playlist', 'firstStart'],
    data() {
      return {
        songIndex2: -1,
        title: '',
        artist: '',
        picture: '',
        autoplay: false,
        flag: false
      };
    },
    created() {
      if (this.firstStart == true) {
        this.GetNewSong(1);
        this.$emit('first-load-done');
      } else {
        this.UpdateTrackInfo();
      }
    },
    mounted() {
      if (myAudio.currentTime != 0) {
        $(myAudio).on('timeupdate', () => {
          this.autoplay = true;
        });
      }
      $(myAudio).on('ended', () => {
        this.GetNewSong(1);
      });
    },
    methods: {
      GetNewSong(step = 1) {
        this.songIndex2 = getSongIndex();
        if (this.songIndex2 + step >= this.playlist.length) this.songIndex2 = 0;
        else if (this.songIndex2 + step < 0) this.songIndex2 = 0;
        else this.songIndex2 += step;
        setSongIndex(this.songIndex2);
        setSongName(this.playlist[this.songIndex2]);
        const songUrl =
          musicUrl + 'play/' + this.playlist[this.songIndex2] + '/';
        Axios({
          method: 'get',
          url: songUrl + 'stats'
        }).then(res => {
          try {
            let songStats = res.data;
            setThisSong(songStats);
            this.title = songStats.title;
            this.artist = songStats.artist;
            this.picture = songStats.picture;
            if (this.picture) {
              this.picture = this._imageEncode(this.picture.data.data);
            } else {
              this.picture = 'https://i.imgur.com/1Y5Uhk6.png';
            }
          } catch (err) {
            console.error(err.message);
          }
          this.title = this.title || 'Unknown title';
          this.artist = this.artist || 'Unknown artist';

          myAudio.src = songUrl;
          myAudio.load();
          if (this.autoplay == true) {
            myAudio.play();
          }
        });
      },
      UpdateTrackInfo() {
        var thisSong = getThisSong();
        this.title = thisSong.title;
        this.artist = thisSong.artist;
        this.picture = thisSong.picture;
        this.title = this.title || 'Unknown title';
        this.artist = this.artist || 'Unknown artist';
        this.picture = this._imageEncode(this.picture.data.data);
      },
      _imageEncode(arrayBuffer) {
        let b64encoded = btoa(
          [].reduce.call(
            new Uint8Array(arrayBuffer),
            (p, c) => {
              return p + String.fromCharCode(c);
            },
            ''
          )
        );
        let mimetype = 'image/jpeg';
        return 'data:' + mimetype + ';base64,' + b64encoded;
      },
      musicPlay() {
        this.autoplay = true;
        myAudio2.pause();
        myAudio.play();
      },
      musicPause() {
        this.autoplay = false;
        myAudio.pause();
      },
      musicPrev() {
        this.GetNewSong(-1);
      },
      musicNext() {
        this.GetNewSong(1);
      }
    },
    components: {
      Information,
      Player
    }
  };
</script>
