<template>
  <div id="Player">
    <footer class="player">
      <div class="container">
        <div class="row justify-content-between pt-2 pb-2 align-items-center">
          <div class="col-sm-12 col-md-4 d-flex">
            <div
              id="player-previous-button"
              @click="$emit('prev-track')"
              class="player__button"
            >
              <font-awesome-icon icon="backward" />
            </div>
            <div id="play" class="player__button">
              <font-awesome-icon
                @click="
                  $emit('play-track');
                  play = !play;
                "
                v-if="play == false"
                id="player-play-icon"
                icon="play"
              />
              <font-awesome-icon
                v-else
                id="player-play-icon"
                @click="
                  $emit('pause-track');
                  play = !play;
                "
                icon="stop"
              />
            </div>
            <div
              id="player-next-button"
              @click="$emit('next-track')"
              class="player__button"
            >
              <font-awesome-icon icon="forward" />
            </div>
          </div>
          <div class="col-md-5 col-lg-4">
            <div class="player__volume">
              <div
                id="block-volume-icon"
                @click="muted()"
                class="player__icon-volume"
              >
                <font-awesome-icon
                  v-if="volume_icon == 0"
                  id="volume-icon"
                  icon="volume-mute"
                />
                <font-awesome-icon
                  v-else-if="volume_icon > 0.5"
                  id="volume-icon"
                  icon="volume-up"
                />
                <font-awesome-icon v-else id="volume-icon" icon="volume-down" />
              </div>
              <input
                @input="updateValue()"
                class="player__volume_line"
                id="volume-line"
                type="range"
                name="points"
                step="0.1"
                min="0.0"
                max="1"
                value="1"
              />
            </div>
          </div>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
  import $ from 'jquery';
  import {
    myAudio,
    myAudio2,
    musicUrl,
    setSongName,
    setThisSong,
    getThisSong
  } from '../assets/js/Audio';
  export default {
    name: 'Player',
    data() {
      return {
        play: false,
        lastVolume: 1,
        volume_icon: 1,
        songName: '',
        title: '',
        artist: '',
        picture: ''
      };
    },

    mounted() {
      if (myAudio.currentTime != 0) {
        $(myAudio).on('timeupdate', () => {
          this.play = true;
          this.updateVolume();
        });
      }

      $(myAudio).on('play', () => {
        this.play = true;
      });

      $(myAudio).on('pause', () => {
        this.play = false;
      });
    },
    methods: {
      UpdateTrackInfo() {
        var thisSong = getThisSong();
        this.title = thisSong.title;
        this.artist = thisSong.artist;
        this.picture = thisSong.picture;
        this.title = this.title || 'Unknown title';
        this.artist = this.artist || 'Unknown artist';
        $('#track-name').html(this.title + ' - ' + this.artist);
        if (this.picture) {
          $('#track-image').prop(
            'src',
            this._imageEncode(this.picture.data.data)
          );
        } else {
          $('#track-image').prop('src', 'https://i.imgur.com/1Y5Uhk6.png');
        }
      },
      muted() {
        if (myAudio.volume != 0) {
          this.lastVolume = myAudio.volume;
          myAudio.volume = 0;
          $('#volume-line').prop('value', 0);
          this.updateValue();
        } else {
          myAudio.volume = this.lastVolume;
          $('#volume-line').prop('value', this.lastVolume);
          this.updateValue();
        }
      },
      updateValue() {
        this.volume_icon = document.getElementById('volume-line').value;
        myAudio.volume = document.getElementById('volume-line').value;
      },
      updateVolume() {
        this.volume_icon = myAudio.volume;
        document.getElementById('volume-line').value = myAudio.volume;
      }
    }
  };
</script>

<style lang="scss">
  @import '../variables';
  .player {
    box-shadow: 0px -3px 7px 5px rgba(0, 0, 0, 0.4);
    background: $Thoroddsenjökull-3-hex;
    width: 100%;
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 0;

    &__volume {
      display: none;

      @media screen and (min-width: 768px) {
        display: block;
      }
    }

    &__button {
      cursor: pointer;
      font-size: 30px;
      color: $Thoroddsenjökull-4-hex;
      margin: 0 auto;

      &:hover {
        color: $Thoroddsenjökull-5-hex;
        animation: buttons-hover 1s;
      }
    }

    &__volume_line {
      outline: none;
      -webkit-appearance: none;
      background-color: $Thoroddsenjökull-5-hex;
      height: 2px;
      position: absolute;
      top: 22px;
      left: 136px;
    }

    &__icon-volume {
      width: 30px;
      float: left;
      font-size: 30px;
      position: relative;
      left: 78px;
      color: $Thoroddsenjökull-4-hex;

      &:hover {
        color: $Thoroddsenjökull-5-hex;
        cursor: pointer;
        animation: buttons-hover 1s;
      }
    }
  }

  input[type='range']::-webkit-slider-thumb {
    vertical-align: middle;
    -webkit-appearance: none;
    background-color: $Thoroddsenjökull-4-hex;
    width: 16px;
    height: 16px;
    border-radius: 16px;
  }
</style>
