<template>
  <Loader v-if="load" />
  <div v-else id="information" class="information_body">
    <div class="container">
      <div class="information">
        <div
          class="row information_height text-center justify-content-center align-items-center"
        >
          <div class="row container">
            <div
              class="col-sm-12 offset-lg-4 col-lg-4 pb-2 pt-2 pt-md-3 pb-md-3"
            >
              <div class="information__img" id="track-image-container">
                <img id="track-image" v-bind:src="picture" />

                <div class="information__btn">
                  <font-awesome-icon @click="addTrack()" icon="thumbs-up" />
                </div>
              </div>
            </div>
            <div class="col-12 pb-2 pt-2 pt-md-3 pb-md-3">
              <h3 class="information__title" id="track-name">
                {{ title + ' - ' + artist }}
              </h3>
            </div>
            <div class="col-12">
              <div class="information__numeric row justify-content-center pt-2">
                <div class="col-3 information__numeric_currentTime">
                  {{ curtime }}
                </div>
                <div class="col-3 information__numeric_songLength">
                  {{ duration }}
                </div>
              </div>
              <input
                disabled
                class="information__currentTime"
                id="current-time"
                type="range"
                name="points"
                step="1"
                min="0"
                value="0"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { accessToken, refreshTokens } from '../assets/js/userApi';
  import { myAudio, getSongName, getThisSong } from '../assets/js/Audio';
  import Loader from './Loader';
  import $ from 'jquery';
  import Axios from 'axios';
  export default {
    name: 'Information',
    props: ['title', 'artist', 'picture'],
    components: {
      Loader
    },
    data() {
      return {
        load: true,
        curtime: '0:00',
        duration: 0,
        picture2: 'https://i.imgur.com/1Y5Uhk6.png'
      };
    },
    created() {
      setTimeout(() => {
        this.load = false;
      }, 200);
    },
    mounted() {
      this.duration =
        parseInt(myAudio.duration / 60) +
        ':' +
        this.PrettyTimeFormat(parseInt(myAudio.duration % 60));
      $(myAudio).on('durationchange', () => {
        this.duration =
          parseInt(myAudio.duration / 60) +
          ':' +
          this.PrettyTimeFormat(parseInt(myAudio.duration % 60));
      });

      $(myAudio).on('timeupdate', () => {
        this.curtime = myAudio.currentTime;
        $('#current-time')
          .attr('value', this.curtime)
          .attr('max', myAudio.duration);
        this.curtime =
          parseInt(myAudio.currentTime / 60) +
          ':' +
          this.PrettyTimeFormat(parseInt(myAudio.currentTime % 60));
      });
    },
    methods: {
      addTrack() {
        Axios({
          method: 'post',
          url: 'https://dailyradio-server.herokuapp.com/favorites/add',
          headers: {
            Authorization: 'Bearer ' + accessToken
          },
          data: {
            song: getSongName()
          }
        })
          .then(res => {})
          .catch(error => {
            refreshTokens(this.addTrack());
          });
      },
      _imageEncode(arrayBuffer) {
        let b64encoded = btoa(
          [].reduce.call(
            new Uint8Array(arrayBuffer),
            (p, c) => {
              return p + String.fromCharCode(c);
            },
            ''
          )
        );
        let mimetype = 'image/jpeg';
        return 'data:' + mimetype + ';base64,' + b64encoded;
      },
      PrettyTimeFormat(num) {
        return num < 10 ? `0${num}` : num;
      }
    }
  };
</script>

<style lang="scss">
  @import '../variables';
  .information {
    color: #324034;
    &__logo {
      font-size: 28px;

      @media screen and (min-width: 768px) {
        font-size: 40px;
      }
    }

    &__img img {
      border: 1px solid #071726;
      height: 250px;
      width: auto;
      box-shadow: 2px 2px 10px 4px rgba(0, 0, 0, 0.3);
      @media screen and (min-width: 768px) {
        height: 300px;
      }
    }
    &__img:hover img {
      filter: blur(2px);
    }

    &__btn {
      cursor: pointer;
      transform: translate(-50%, -50%);
      color: #324034;
      font-size: 32px;
      position: absolute;
      left: 50%;
      top: 50%;
      &:hover {
        color: #4c5939;
        img {
          filter: blur(1px);
        }
      }
    }

    &__title {
      height: 2em;
      margin: 0 auto;
      font-size: 16px;

      @media screen and (min-width: 768px) {
        font-size: 26px;
      }
    }

    &__numeric {
      font-size: 12px;

      @media screen and (min-height: 768px) {
        font-size: 24px;
      }
    }

    &__currentTime {
      outline: none;
      -webkit-appearance: none;
      background-color: #4c5939;
      width: 260px;
      height: 2px;
      border-radius: 15px;
    }
  }
</style>
