<template>
  <div id="app">
    <Header />
    <Loader v-if="load" />
    <router-view
      @first-load-done="Done()"
      v-else
      :playlist="playlist"
      :firstStart="firstStart"
    />
  </div>
</template>

<script>
  import Header from './components/Header.vue';
  import Loader from './components/Loader';
  import Axios from 'axios';
  export default {
    name: 'app',
    components: {
      Header,
      Loader
    },
    data() {
      return {
        playlist: [],
        load: true,
        firstStart: true
      };
    },
    created() {
      this.GetPlayList();
    },
    methods: {
      Done() {
        this.firstStart = false;
      },
      GetPlayList() {
        Axios({
          method: 'get',
          url: 'https://dailyradio-server.herokuapp.com/music/playlist/'
        }).then(res => {
          this.playlist = res.data;
          this.load = false;
        });
      }
    }
  };
</script>

